.display2 {
  font-weight: 600;
  font-size: 40px;
  line-height: 120%;
}
.btn-blue {
  flex-direction: column;
  background: #004288;
  border-radius: 50%;
  border: 3px solid #004288;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.2);
  width: 131px;
  height: 131px;
  position: relative;
  i {
    width: auto;
    height: auto;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  span {
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    margin-top: 5px;
  }
}
.video {
  &--active {
    &:after {
      display: none;
    }
    .advantages__video-play {
      display: none;
    }
  }
}
.nice-select {
  background: #FFFFFF;
  border: 2px solid #004288;
  border-radius: 5px;
  height: unset;
  padding: 15px;
  &:after {
    top: 50%;
    right: 15px;
    transform: translate(0, -50%);
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px 6px;
    width: 10px;
    height: 6px;
  }
  &:hover {
    border-color: #004288;
  }
  .option {
    font-size: 18px;
    padding: 15px;
  }
}
.open {
  border-color: #004288;
  &:after {
    transform: rotate(180deg) !important;
  }
}
.current {
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #000000;
}

@media screen and (max-width: 1100px) {
  .display2 {
    font-size: 35px;
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    font-size: 26px;
  }
  .btn-blue {
    width: 86px;
    height: 86px;
    i {
      width: 17px;
    }
    span {
      font-size: 11px;
      margin-top: 2px;
    }
  }
}