.footer {
  background: #FFFFFF;
  box-shadow: 0px 0px 20px #C9C9C9;
  padding: 90px 0 30px;
  &-top {
    display: flex;
    justify-content: space-between;
    padding-bottom: 60px;
    &__right {
      display: flex;
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    border-top: 1px solid #E0E0E0;
    &__info {
      display: flex;
      align-items: center;
    }
    &__studio {
      font-weight: 500;
      font-size: 15px;
      line-height: 120%;
    }
    &__politics {
      font-size: 15px;
      line-height: 120%;
      color: #808080;
      margin-left: 15px;
    }
    &__socials {
      display: flex;
      align-items: center;
    }
    &__social {
      padding: 5px;
      svg path {
        transition: all 0.5s;
      }
      &:hover {
        svg path {
          fill: #E22427;
        }
      }
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
  }
  &__tel {
    font-weight: 500;
    font-size: 22px;
    line-height: 105%;
    display: block;
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
  &__location {
    display: flex;
    align-items: center;
    margin-top: 15px;
    i {
      width: 34px;
      height: 34px;
      background: #E22427;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
    }
    span {
      font-weight: 500;
      font-size: 16px;
      line-height: 105%;
      text-decoration: underline;
      margin-left: 12px;
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    margin-top: 15px;
    i {
      width: 34px;
      height: 34px;
      background: #E22427;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
    }
    span {
      font-weight: 500;
      font-size: 16px;
      line-height: 105%;
      text-decoration: underline;
      margin-left: 12px;
    }
  }
  &__list {
    &:not(:first-child) {
      margin-left: 100px;
    }
    li {
      font-weight: 500;
      font-size: 16px;
      line-height: 105%;
      color: #808080;
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
  &__name {
    font-size: 18px !important;
    color: #000 !important;
  }

}

@media screen and (max-width: 800px) {
  .footer {
    &-top {
      flex-direction: column;
      padding-bottom: 0;
      &__left {
        padding-top: 40px;
        border-top: 1px solid #E0E0E0;
      }
      &__right {
        display: block;
        order: -1;
        padding-bottom: 40px;
      }
    }
    &-bottom {
      flex-direction: column;
      padding-top: 50px;
      border: none;
      &__info {
        flex-direction: column;
        justify-content: space-between;
        align-items: start;
        padding-top: 30px;
      }
      &__politics {
        margin: 5px 0 0 0;
      }
      &__socials {
        order: -1;
        padding-bottom: 30px;
        border-bottom: 1px solid #E0E0E0;
      }
    }
    &__tel {
      font-size: 20px;
    }
    &__list {
      &:not(:first-child) {
        margin: 25px 0 0 0;
      }
    }
  }
}