.header {
  overflow: hidden;
&__wrapp {
  position: fixed;
  z-index: 4;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  padding: 23px 0;
}
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    max-width: 155px;
    display: block;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li:not(:first-child) {
      margin-left: 15px;
    }
  }
  &__link {
    font-weight: 500;
    font-size: 15px;
    line-height: 120%;
  }
  &__item {
    position: relative;
    &--active {
      .header__item-show {
        span {
          color: #E22427;
        }
        i {
          transform: rotate(-180deg);
          svg path {
            fill: #E22427;
          }
        }
      }
      .header__item-hidden {
        opacity: 1;
        pointer-events: unset;
        transition: all 0.3s;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      cursor: default;
      transition: all 0.2s;
      &:hover {
       span {
         color: #E22427;
       }
        i {
          transform: rotate(-180deg);
          svg path {
            fill: #E22427;
          }
        }
      }
      span {
        font-weight: 500;
        font-size: 15px;
        line-height: 120%;
        margin-right: 5px;
      }
    }
    &-hidden {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      min-width: 315px;
      opacity: 0;
      pointer-events: none;
      padding-top: 70px;
    }
    &-link {
      font-weight: 500;
      font-size: 17px;
      line-height: 120%;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__hidden-inner {
    background: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    padding: 30px;
  }
  &__tel {
    display: none;
  }
  &__btn {
    background: #E22427;
    border-radius: 50px;
    border: 1px solid #E22427;
    width: 194px;
    height: 38px;
    &:hover {
      background: #fff;
      i {
        svg path {
          fill: #000;
        }
      }
      span {
        color: #000;
      }
    }
    span {
      font-weight: 500;
      font-size: 13px;
      line-height: 120%;
      color: #fff;
      margin-left: 6px;
    }
  }
  &__burger {
    width: 26px;
    height: 14px;
    position: relative;
    z-index: 6;
    cursor: pointer;
    margin-left: 30px;
    &--active {
      span {
        &:nth-child(1) {
          transform: rotate(-45deg);
          top: 50% !important;
        }
        &:nth-child(2) {
         display: none;
        }
        &:nth-child(3) {
          transform: rotate(45deg);
          bottom: unset !important;
          width: 100% !important;
          top: 50%;
        }
      }
    }
    span {
      height: 2.5px;
      background: #000;
      position: absolute;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
        left: 0;
        width: 100%;
      }
      &:nth-child(2) {
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 100%;
      }
      &:nth-child(3) {
        left: 0;
        bottom: 0;
        width: 70%;
      }
    }
  }
  &-menu {
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #FFFFFF;
    padding: 23px 15px 30px 15px;
    opacity: 0;
    transition: all 1s;
    pointer-events: none;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
    &__wrapp {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      max-width: 1200px;
      margin: 0 auto;
      overflow-y: auto;
      overflow-x: hidden;
      padding-right: 10px;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #000;
        border-radius: 3px;
      }
    }
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__close {
      width: 26px;
      height: 14px;
      position: relative;
      z-index: 5;
      cursor: pointer;
      span {
        height: 2.5px;
        background: #000;
        position: absolute;
        transition: all 0.5s;
        &:nth-child(1) {
          width: 100%;
          transform: rotate(-45deg);
          top: 50%;
        }
        &:nth-child(2) {
          width: 100%;
          transform: rotate(45deg);
          top: 50%;
        }
      }
    }
    &__logo {
      width: 155px;
      display: block;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &__items {
      display: grid;
      grid-template-columns: 1.5fr 1.5fr 1fr;
      gap: 60px;
      margin-top: 30px;
    }
    &__item {
      display: flex;
      &-img {
        width: 36px;
        height: 36px;
        background: #E22427;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        margin-right: 15px;
        svg path {
          fill: #fff;
        }
      }
      &-desc {
        flex-grow: 1;
      }
      &-title {
        font-size: 30px;
        line-height: 120%;
        font-weight: 600;
        margin-bottom: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        i {
          display: none;
        }
      }
      &-list {
        a {
          font-size: 18px;
          line-height: 120%;
          font-weight: 500;
          color: #676767;
          display: block;
          &:not(:first-child) {
            margin-top: 25px;
          }
          &:hover {
            color: #E22427;
          }
        }
      }
      &-close {
        align-items: center;
        margin-bottom: 25px;
        display: none;
        span {
          font-weight: 500;
          font-size: 16px;
          line-height: 120%;
          margin-left: 7px;
        }
      }
    }
    &__hide-title {
      font-size: 21px;
      line-height: 120%;
      font-weight: 500;
      color: #676767;
      display: none;
    }
    &__connection {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__tel {
      font-size: 24px;
      line-height: 120%;
      font-weight: 600;
    }
    &__mail {
      display: flex;
      align-items: center;
      margin-top: 10px;
      i {
        width: 36px;
        height: 35px;
        background: #E22427;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
      }
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 500;
        margin-left: 7px;
        text-decoration: underline;
      }
    }
    &__btn {
      width: 131px;
      height: 131px;
      background: #E22427;
      border-radius: 50%;
      border: 3px solid #E22427;
      flex-direction: column;
      i {
        width: 27px;
        height: auto;
        svg {
          width: 100%;
          height: 100%;
        }
      }
      span {
        font-weight: 500;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        margin-top: 5px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__center {
      display: none;
    }
    &__tel {
      display: block;
      margin-right: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__wrapp {
      z-index: 7;
    }
    &-menu {
      background-color: rgba(0, 0, 0, 0.3);
      padding: 0;
      &--active {
        .header-menu__wrapp {
          right: 0;
          transition: all 1s;
        }
      }
      &__wrapp {
        background: #fff;
        max-width: 450px;
        margin: 0 0 0 auto;
        position: relative;
        right: -100%;
        transition: all 1s;
        padding: 115px 15px 15px;
      }
      &__top {
        display: none;
      }
      &__items {
        display: flex;
        flex-direction: column;
        gap: 0;
        margin: 0;
      }
      &__item {
        &--order {
          order: 2;
        }
        &:not(:first-child) {
          margin-top: 25px;
        }
        &-title {
          font-size: 20px;
          margin-bottom: 0;
          position: relative;
          padding-left: 36px;
          cursor: pointer;
          &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 26px;
            height: 26px;
            background: #E22427;
            border-radius: 50%;
          }
          &:before {
            content: '';
            position: absolute;
            z-index: 2;
            top: 50%;
            left: 9px;
            transform: translate(0, -50%);
            width: 7px;
            height: 7px;
            background-image: url("../img/arrow2.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: 7px 7px;
          }
          i {
            display: block;
          }
        }
        &-list {
          position: absolute;
          z-index: 5;
          top: 0;
          right: -100%;
          height: 100%;
          width: 100%;
          padding: 115px 15px 15px;
          background: #fff;
          transition: all 0.5s;
          &--active {
            right: 0;
          }
          &2 {
            position: static;
            z-index: 1;
            padding: 0;
          }
        }
        &-close {
          display: flex;
          cursor: pointer;
        }
        &-img {
          display: none;
        }
      }
      &__hide-title {
        display: block;
      }
      &__btn {
        width: 102px;
        height: 102px;
        i {
          width: 30px;
          height: 30px;
        }
      }
      &__tel {
        font-size: 20px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &-menu {
      &__wrapp {
        max-width: 100%;
      }
      &__btn {
        width: 86px;
        height: 86px;
        i {
          width: 17px;
        }
        span {
          font-size: 11px;
          margin-top: 2px;
        }
      }
    }
    &__logo {
      width: 125px;
    }
    &__tel {
      margin-right: 22px;
    }
    &__btn {
      width: 34px;
      height: 34px;
      span {
        display: none;
      }
    }
    &__burger {
      margin-left: 22px;
    }
  }
}