.main {
  position: relative;
  padding: 340px 0 120px 0;
  overflow: hidden;
  &__inner {
    padding-bottom: 300px;
  }
  &__left {
    max-width: 540px;
  }
  &__right {
    position: absolute;
    top: 120px;
    right: -190px;
    max-width: 1100px;
    transition: all 1.8s;
    img {
      width: 100%;
    }
  }
  .main__right.active {
    transform: rotate(-12deg);
  }
  &-img__mob {
    display: none;
  }
  &__title {
    transform: translate(0px, 120%);
    opacity: 0;
    pointer-events: all;
    transition: all 0.8s;
    span {
      display: block;
      color: #004288;
    }
  }
  &__title.active {
    transform: translate(0,0);
    opacity: 1;
    pointer-events: all;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-top: 25px;
    padding-left: 27px;
    position: relative;
    transform: translate(0px, 100px);
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
    &:after {
      content: '';
      position: absolute;
      top: 9px;
      left: 0;
      width: 19px;
      height: 2px;
      background: #000;
    }
  }
  &__subtitle.active {
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: all;
    transition: all 0.8s ease 0.4s;
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      span {
        color: #E22427;
      }
      svg {

      }
    }
    i {
      width: 27px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      color: #808080;
      margin-left: 8px;
    }
  }
}
.info {
  background: #EEF4FB;
  padding: 90px 0;
  overflow: hidden;
  &__desc {
    font-size: 20px;
    line-height: 130%;
    text-align: center;
    max-width: 1020px;
    margin: 0 auto;
    transform: translate(0, 120%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
    a {
      font-weight: 500;
      color: #004288;
      text-align: center;
      &:hover {
        color: #E22427;
      }
    }
  }
  &__desc.active {
    transform: translate(0,0);
    pointer-events: all;
    opacity: 1;
  }
}
