    background-position: center;
    background-size: 100% 625px;
    margin-top: 80px;
  }
  &__video {
    width: 100%;
    height: 100%;
    cursor: pointer;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 65px;
      height: 65px;
      background: transparent;
      border: 2px solid #004288;
      border-radius: 50%;
    }
    iframe {
      width: 100%;
      height: 100%;
    }
    &-play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      justify-content: center;
      align-items: center;
      width: 53px;
      height: 53px;
      background: #004288;
      border-radius: 50%;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
    }
  }
  &__btn {
    position: absolute;
    z-index: 2;
    top: -65px;
    right: -65px;
