    opacity: 1;
    pointer-events: all;
    transition: all 0.8s ease 0.4s;
  }
  &__title {
    transform: translate(0, 120%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
  }
  &__title.active {
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: all;
  }
}
