.team {
  padding: 140px 0;
  overflow: hidden;
  &__title {
    text-align: center;
    transform: translate(0, 120%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
  }
  &__title.active {
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: all;
  }
  &__slider {
    position: relative;
    margin-top: 60px;
  }
  .swiper-slide {
    text-align: center;
  }
  &__slide {
    &-img {
      width: 106px;
      height: 106px;
      margin: 0 auto;
      transform: scale(0.5);
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      img {
        width: 100%;
      }
    }
    &-img.active {
      transform: scale(1);
      opacity: 1;
      pointer-events: all;
    }
    &-desc {
      margin-top: 10px;
    }
    &-name {
      font-weight: 600;
      font-size: 19px;
      line-height: 120%;
      display: block;
    }
    &-post {
      font-size: 16px;
      line-height: 120%;
      margin-top: 5px;
      display: block;
    }
  }
  .swiper-button-next {
    right: -50px;
    &:after {
      display: none;
    }
  }
  .swiper-button-prev {
    left: -50px;
    transform: rotate(-180deg);
    &:after {
      display: none;
    }
  }
  .swiper-pagination {
    bottom: -47px;
    display: none;
    &-bullet {
      background: #004288;
    }
  }
}
.connection {
  overflow: hidden;
  &__inner {
    background: #F1F2F5;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
    overflow: hidden;
  }
  &__right {
    display: flex;
    align-items: center;
    max-width: 520px;
  }
  &__title {
    font-weight: 600;
    font-size: 30px;
    line-height: 120%;
    span {
      color: #004288;
    }
  }
  &__desc {
    font-size: 16px;
    line-height: 120%;
  }
  &__application {
    text-decoration: underline;
    display: inline-block;
    padding: 0;
  }
  &__tel {
    font-weight: 500;
  }
  &__btn {
    flex-shrink: 0;
    margin-left: 60px;
    opacity: 0;
    transition: all 0.8s;
  }
  &__btn.active {
    opacity: 1;
  }
}
.projects {
  padding: 90px 0;
  position: relative;
  overflow: hidden;
  &__img-bg {
    position: absolute;
    z-index: -1;
    left: 50%;
    bottom: -430px;
    transform: translate(-50%, 0);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-align: center;
    transform: translate(0, 120%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
  }
  &__title.active {
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: all;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px 80px;
    margin-top: 60px;
  }
  &__card {
    transform: translate(-120%, 0);
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
    &--2 {
      margin-top: 80px;
      transform: translate(120%, 0);
    }
    &:hover {
      .projects__card-title {
        color: #000;
      }
      .projects__card-img {
        img {
          transform: scale(1.1);
        }
      }
    }
    &-img {
      overflow: hidden;
      img {
        transition: all 0.5s;
        width: 100%;
      }
    }
    &-title {
      font-weight: 500;
      font-size: 21px;
      line-height: 120%;
      margin-top: 15px;
    }
    &-hashtags {
      display: flex;
      align-items: center;
      margin-top: 10px;
      span {
        font-weight: 500;
        font-size: 14px;
        line-height: 120%;
        color: #808080;
        &:not(:first-child) {
          margin-left: 14px;
        }
      }
    }
  }
  &__card.active {
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: all;
    &:nth-child(1) {
      transition: all 0.8s ease 0.4s;
    }
    &:nth-child(2) {
      transition: all 0.8s ease 0.6s;
    }
    &:nth-child(3) {
      transition: all 0.8s ease 0.8s;
    }
    &:nth-child(4) {
      transition: all 0.8s ease 1s;
    }
  }
  &__btn {
    flex-direction: column;
    background: #E22427;
    border-radius: 50%;
    border: 3px solid #E22427;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.2);
    width: 131px;
    height: 131px;
    margin: 60px auto 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.8s;
    span {
      bottom: 15px;
    }
  }
  &__btn.active {
    opacity: 1;
    pointer-events: all;
  }
}

